import 'vant/es/time-picker/style/index';
import { ref } from 'vue';
const year = new Date().getFullYear();
const month = new Date().getMonth() + 1;
const date = new Date().getDate();
//console.log(year, month, date)
const currentDate = ref([year, month - 1, date]);
let that = this;
const isBlock = false;
export default {
  setup() {
    const days = 24 * 3600 * 1000;
    const showToolBar = false;
    const showTitle = false;
    let bb = "白班";
    let yb = "夜班";
    let xx = '休息';
    const dayInterval = 5;
    const defaultTime = '2023/02/01';
    let minD = new Date(year, month - 1, 1);
    //计算最后一天
    let maxD = new Date(new Date(year, month, 1) - days);
    const formatter = day => {
      // const year = day.date.getFullYear() ;
      // const month = day.date.getMonth() ;
      // const date = day.date.getDate();
      // const newDate= year+'/'+month+'/'+date
      //console.log(day,newDate)
      const distance = day.date - new Date(defaultTime);
      //console.log(distance)
      let nl = day.date.toLocaleString('zh-CN-u-ca-chinese').replace(/(\d+)\s*?年/, (_, y) => "甲乙丙丁戊己庚辛壬癸".charAt((y - 4) % 10) + "子丑寅卯辰巳午未申酉戌亥".charAt((y - 4) % 12));
      nl = nl.slice(0, 6);
      nl = nl.slice(2, 6);
      day.topInfo = nl;
      if (distance >= 0) {
        const num = Math.floor(distance / days);
        let numday = Math.floor(num % dayInterval);
        if (numday == 0) {
          day.bottomInfo = bb;
        } else if (numday == 1) {
          day.bottomInfo = yb;
        } else {
          day.bottomInfo = xx;
        }
      } else {
        const num = Math.floor(Math.abs(distance) / days);
        let numday = Math.floor(num % dayInterval);
        //console.log(numday,44444,day.date)
        if (numday == 0) {
          day.bottomInfo = bb;
        } else if (numday == 4) {
          day.bottomInfo = yb;
        } else {
          day.bottomInfo = xx;
        }
      }
      return day;
    };
    const getDate = value => {
      let time = value.selectedValues;
      let year = time[0];
      let month = time[1];
      let date = time[2];
      let toDate = new Date(year, month, date);
      currentDate.value = time;
      that.$refs.calendar.scrollToDate(toDate);
      //console.log(value)
    };

    return {
      formatter,
      minDate: new Date(defaultTime),
      maxDate: new Date(2030, 10, 1),
      currentDate,
      getDate,
      maxD,
      minD,
      showToolBar,
      showTitle,
      isBlock
    };
  }
};